<template>
    <div class="wrapper">
        <el-row>
            <el-button type="primary">新增</el-button>
        </el-row>
        <div class="tableCon">
            <template>
                <el-table
                    :data="tableData"
                    style="width: 100%; border: 1px solid #eee;border-bottom: none;">
                    <el-table-column
                    label="人员名称"
                    width="280">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.name }}</span>
                    </template>
                    </el-table-column>
                    <el-table-column
                        label="角色"
                        width="280">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.date }}</span>
                    </template>
                    </el-table-column>
                    <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                    </el-table-column>
                </el-table>
            </template>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            tableData: [{
            date: '2016-05-02',
            name: '王小虎'
            }]
        }
    }
}
</script>
<style lang="stylus" scoped>
.tableCon
    border-radius 12px
    overflow hidden
    margin-top 12px
    background #fff
    padding 20px
</style>



